.attractions {
  padding: 8rem 0 0; }
  .attractions__title {
    font-size: 2.25rem;
    margin: 0;
    font-style: normal;
    font-weight: 600;
    padding: 1rem 0;
    border-bottom: 1px solid #3C8B7B; }
  .attractions__description {
    font-size: 1.5rem;
    text-align: center; }

.contact {
  padding: 8rem 0 0; }
  .contact__title {
    font-size: 2.25rem;
    margin: 0;
    font-style: normal;
    font-weight: 600;
    padding: 1rem 0;
    border-bottom: 1px solid #3C8B7B; }
  .contact__box {
    font-size: 1.5em;
    display: grid;
    grid-template-columns: 0.5fr 1fr;
    padding: 8rem 0; }
    @media (max-width: 850px) {
