      justify-content: center;
      color: #653cad;
      white-space: nowrap; }
      .contact__box-data-list {
        padding-left: 0; }
        .contact__box-data-list-item {
          display: flex;
          align-items: center;
          text-align: left; }
          .contact__box-data-list-item a {
            color: #653cad;
            text-decoration: none; }
          .contact__box-data-list-item p {
            margin: 0.5rem 0; }
    .contact__box-direction {
      display: flex;
      justify-content: center;
      padding: 1rem;
      margin: 1rem; }

.gallery {
  padding: 8rem 0 0; }
  .gallery__container {
    margin-left: auto;
    margin-right: auto;
    display: block; }
  .gallery__title {
    font-size: 2.25rem;
    margin: 0;
    font-style: normal;
    font-weight: 600;
    padding: 1rem 0;
    border-bottom: 1px solid #3C8B7B; }
  .gallery img {
    width: 200px;
    height: 150px;
    transition: 1s;
    padding: 1rem; }
    .gallery img:hover {
      filter: grayscale(100%);
      transform: scale(1.1); }

.price {
  padding: 8rem 0 0; }
  .price__title {
    font-size: 2.25rem;
    margin: 0;
