    font-weight: 600;
    padding: 1rem 0;
    border-bottom: 1px solid #3C8B7B; }
  .price__table {
    margin-top: 8rem;
    font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
    font-size: 1.5rem;
    border-collapse: collapse;
