    color: white; }

html {
  box-sizing: border-box;
  font-size: 10px;
  scroll-behavior: smooth; }
  @media (max-width: 850px) {
    html {
      font-size: 12px; } }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  color: #972C73;
  overflow-x: hidden;
  font-family: "Lato", sans-serif; }

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: rgba(10, 7, 14, 0.95); }

.main {
  max-width: 1245px;
  padding: 30px 16px 0 16px;
  margin: 0 auto; }

.navigation {
  max-width: 1216px;
  padding: 4px 16px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  text-transform: uppercase;
  font-size: 1.5rem;
  white-space: nowrap; }
  @media (max-width: 850px) {
    .navigation {
      font-size: 1rem; } }
  .navigation__list {
    display: flex;
    align-items: center; }
    .navigation__list-toggle {
      display: flex;
      align-items: center;
      padding: 10px 20px;
      font-size: 20px;
      box-sizing: border-box;
      display: none; }
      @media (max-width: 850px) {
        .navigation__list-toggle {
          display: block; } }
  @media (max-width: 850px) {
    .navigation ul {
      width: 100%;
      display: none; } }
  @media (max-width: 850px) {
    .navigation .toggleClass {
      margin-top: 0px;
      padding-top: 40px;
      display: block; } }
  .navigation li {
    list-style: none;
    transform: translateX(100rem);
    animation: slideIn 0.5s forwards; }
    @media (max-width: 850px) {
      .navigation li {
        display: block;
        text-align: center;
        padding: 8px 0; } }
  .navigation a {
    text-decoration: none;
    padding: 1rem 0;
    margin: 0 3rem;
    position: relative;
    letter-spacing: 2px;
    color: #972C73; }
    @media (max-width: 850px) {
      .navigation a {
        margin-top: 40px; }
        .navigation a:hover::before, .navigation a:hover::after {
          display: none; } }
    .navigation a::before, .navigation a::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      background-color: #3C8B7B;
      left: 0;
      transform: scaleX(0);
      transition: all 0.5s; }
    .navigation a::before {
      top: 0;
      transform-origin: left; }
    .navigation a::after {
      bottom: 0;
      transform-origin: right; }
    .navigation a:last-child {
      margin-right: 0; }
    .navigation a:hover::before, .navigation a:hover::after {
      transform: scaleX(1); }

.navigation-logo {
  height: 70px;
  transform: translateX(-100rem);
  animation: slideIn 0.5s forwards;
  display: flex;
  justify-content: center;
  margin-right: 20px; }
  @media (max-width: 850px) {
    .navigation-logo {
      display: none; } }
  .navigation-logo-mobile {
    display: none; }
    @media (max-width: 850px) {
      .navigation-logo-mobile {
        display: flex;
        align-items: center;
        height: 40px; } }
  .navigation-logo-text {
    display: none;
    padding: 0 16px; }
    @media (max-width: 850px) {
      .navigation-logo-text {
        display: block;
        align-items: center;
        z-index: 3;
        height: 40px; } }

.slides {
  padding-top: 100px;
  display: flex;
  justify-content: center; }
  @media (max-width: 850px) {
    .slides {
      width: 300px; } }

.footer {
  width: 100%;
